body{
  background-color: black;
  font-family: 'Dancing Script', cursive;
}
.bigouter{
  margin-left: 40px;
  margin-right: 40px;
  margin-top: 20px;
  margin-bottom: 40px;
  width:auto;
  border:4px solid orange;
  border-radius: 54px;
  flex-direction: column;
}
.outerbox{
  width:auto;
  height:auto;
  border: 4px solid black;
  justify-content: center;
  align-items: center;
  border-radius: 44px;
  text-align: center;
  background:linear-gradient(45deg, rgb(139, 135, 135),rgb(59, 53, 46), rgb(50, 43, 39));
  flex-direction: column;
  color:orange;
 
}

.pic{
  width:180px;
  height:180px;
  border-radius: 5px;
  border: 2px solid rgb(223, 185, 113);
  overflow:hidden;
  display:flex;
  justify-content: center;
  margin-left: 35px;
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: black;
}
.notesbox{
  background-image: linear-gradient(45deg, #ffaa18 25%, transparent 25%, transparent 75%, #ffaa18 75%, #ffaa18 100%);
  background-size: 20px 20px;
  background-color: #f89e30;
  border: 4px solid rgb(54, 49, 49);
  width:auto;
  word-break: break-all;
  overflow: hidden;
  border-radius: 8px;
  flex-direction: column;
  color:black;
  
}

button{
  background-color: rgb(54, 49, 49);
  border:2px solid white;
  border-radius: 6px;
  cursor: pointer;
  color: white;

}
button:hover{
  background-color: rgb(193, 193, 230);
  color: black;
}
.header{
  background:linear-gradient(to left, rgb(246, 172, 116),rgb(245, 143, 40), rgb(249, 131, 4)); 
  font-size: 40px;
  padding-bottom: 20px;
  padding-top: 20px;
  text-align: center;
}

  @keyframes blink {
    0%{opacity:1;}
    50%{opacity:0;}
    100%{opacity:1;}
  }
  
